#MainGameDiv {
  display: flex;
  width: 72vw;
  height: 60vh;
  justify-content: space-between;
  align-items: flex-end;
}
#gamediv {
  display: block;
  position: relative;
  width: fit-content;
}

#court {
  width: calc(17vw + 10vh);
  cursor: pointer;
}

.MalePlayerBubble {
  border: calc(0.1vw + 0.2vh) solid #faa159;
  border-radius: calc(0.6vw + 0.2vh);
  padding: 1vh 1vw 1vh 1vw;
  text-align: center;
  position: relative;
}
.MalePlayerBubble::after {
  display: block;
  /*content: url('FirstArrow.png');*/
  background-image: url('./FirstArrow.png');
  background-repeat: no-repeat;
  background-size: contain;
  content: '';
  position: absolute;
  bottom: -25.2%;
  left: 70%;
  height: 25%;
  width: 25%;
}
.MalePlayerDiv {
  position: relative;
  width: 33%;
  margin-right: 4%;
  background-color: white;
}
.MalePlayerBubbleFirstAlt {
  border: calc(0.1vw + 0.2vh) solid #faa159;
  border-radius: calc(0.6vw + 0.2vh);
  padding: 1vh 1vw 1vh 1vw;
  text-align: center;
  position: relative;
}
.MalePlayerBubbleFirstAlt::after,
.MalePlayerBubbleThirdAlt::after {
  display: block;
  /*content: url('FirstArrow.png');*/
  background-image: url('./FirstArrow.png');
  background-repeat: no-repeat;
  background-size: contain;
  content: '';
  position: absolute;
  bottom: -25.2%;
  left: 20%;
  transform: rotateY(180deg);
  height: 25%;
  width: 25%;
}
.MalePlayerBubbleSecondAlt {
  border: calc(0.1vw + 0.2vh) solid #faa159;
  border-radius: calc(0.6vw + 0.2vh);
  padding: 1vh 1vw 1vh 1vw;
  text-align: center;
  position: relative;
}
.MalePlayerBubbleSecondAlt::after {
  display: block;
  /*content: url('FirstArrow.png');*/
  background-image: url('./SecondArrow.png');
  background-repeat: no-repeat;
  background-size: contain;
  content: '';
  position: absolute;
  bottom: -45%;
  transform: rotate(10deg);
  left: 97%;
  height: 64%;
  width: 64%;
}
.MalePlayerBubbleThirdAlt {
  border: calc(0.1vw + 0.2vh) solid #faa159;
  border-radius: calc(0.6vw + 0.2vh);
  padding: 1vh 1vw 1vh 1vw;
  text-align: center;
  position: relative;
}
