.FemalePlayerBubble {
  border: calc(0.1vw + 0.2vh) solid #faa159;
  border-radius: calc(0.6vw + 0.2vh);
  padding: 1vh 1vw 1vh 1vw;
  margin-bottom: 0.1vw;
  text-align: center;
  position: relative;
  background-color: white;
}
.FemalePlayerBubble::after {
  display: block;
  /*content: url('FirstArrow.png');*/
  background-image: url('./FirstArrow.png');
  background-repeat: no-repeat;
  background-size: contain;
  content: '';
  position: absolute;
  bottom: -25.2%;
  left: 10%;
  height: 25%;
  width: 25%;
  transform: rotateY(180deg);
}
.FemalePlayerDiv {
  position: relative;
  width: 33%;
}
.FemalePlayerDiv img {
  margin-right: 32%;
}
.FemalePlayerBubbleFirstAlt {
  border: calc(0.1vw + 0.2vh) solid #faa159;
  border-radius: calc(0.6vw + 0.2vh);
  padding: 1vh 1vw 1vh 1vw;
  text-align: center;
  position: relative;
}
.FemalePlayerBubbleFirstAlt::after {
  display: block;
  background-image: url('./FirstArrow.png');
  background-repeat: no-repeat;
  background-size: contain;
  content: '';
  position: absolute;
  bottom: -25.2%;
  left: 20%;
  transform: rotateY(180deg);
  height: 25%;
  width: 25%;
}

.Situation {
  position: absolute;
  top: -5%;
}
.Situation h1 {
  font-size: 1.6vw;
  font-weight: 900;
  margin: 0;
  margin-bottom: 1vh;
}
.Situation h2 {
  font-size: calc(1vw + 1vh);
  font-weight: 500;
  margin: 0;
}
#GamePress {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  cursor: pointer;
}
