.PopUpContent::after {
  content: '';
  position: absolute;
  left: -3.2vw;
  bottom: 3vh;
  margin-left: -0.46vw;
  width: 0;
  height: 0;
  border-left: 2vw solid transparent;
  border-right: 2vw solid transparent;
  border-top: 3vw solid #0f1259;
  transform: rotate(90deg);
}
.PopUpContent::before {
  content: '';
