.ContactDiv {
  width: 60%;
  height: 90%;
  position: absolute;
  padding: 0;
  direction: rtl;
  right: 22vw;
  top: 7vh;
}

.ContactDiv h1 {
  font-size: 2vw;
}
.ContactDiv p {
  font-size: 1.4vw;
}
.ContactDiv div {
  font-size: 1.4vw;
  list-style-type: none;
  color: #0f1259;
  font-weight: 900;
  display: flex;
  width: fit-content;
  align-items: center;
  margin-top: 2.5vw;
}
.ContactDiv a {
  text-decoration: underline;
  color: #0f1259;
  font-weight: 900;
}
#ContactImg {
  position: absolute;
  top: 3vh;
  left: -13vw;
  width: 30vw;
}
.ContactDiv span {
  margin-right: 1vw;
}
.BlueButton {
  width: 2.2vw;
}
