#RulesMain {
  width: calc(100% - 15vw);
  position: absolute;
  top: 2vw;
  right: 15vw;
  display: flex;
  direction: rtl;
  flex-direction: column;
}

#RulesMainButtonDiv {
  display: flex;
  direction: rtl;
  justify-content: space-evenly;
  margin-right: 4vw;
  margin-top: 0vw;
  height: 100px;
  width: 45vw;
}

.RulesMainButton {
  font-size: 1.5vw;
  width: 10.4vw;
  border: none;
  background-color: rgb(220, 220, 220);
  height: fit-content;
  padding: 0.4em 0em 0.4em 0em;
  transition: 0.5s;
  transform: skewX(20deg);
}

.RulesMainButton p {
  transform: skewX(-20deg);
  padding: 0;
  margin: 0;
}

.RulesMainButtonActive {
  background-color: rgb(150, 150, 150);
}

#Times {
  border: 1px solid black;
  display: flex;
}

.Penalty {
  overflow-y: scroll;
  height: 70vh;
  direction: ltr;
  padding-right: 2vw;
  margin-right: 3vw;
}
.Penalty::-webkit-scrollbar {
  width: 0.5vw;
}
.Penalty::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 10vw;
}

/* Handle */
.Penalty::-webkit-scrollbar-thumb {
  background: rgba(100, 100, 100, 0.3);
  border-radius: 10vw;
}

/* Handle on hover */
.Penalty::-webkit-scrollbar-thumb:hover {
  background: rgba(100, 100, 100, 0.6);
  cursor: pointer;
}

/* Handle on active */
.Penalty::-webkit-scrollbar-thumb:active {
  background: rgba(100, 100, 100, 0.7);
  cursor: pointer;
}

#TimesContentDiv {
  width: 70vw;
  margin-right: 3vw;
  text-align: right;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin-top: -5vh;
}

#SignalContentDiv {
  width: 80vw;
  margin-right: -2vw;
  text-align: right;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 0vh;
}

#SignalContentDiv div {
  width: 19vw;
  height: 30vh;
  text-align: center;
  margin: 0;
  display: block;
  margin-bottom: 6vh;
  margin-top: -1vh;
  border-left: 1px solid black;
}

#SignalContentDiv div p {
  font-size: calc(0.7vw + 1vh);
  color: #0f1359;
  font-weight: 900;
  padding: 0;
  margin: 0;
  margin-top: -3vh;
}

