#SignalContentTopic {
  position: absolute;
  font-size: calc(1vw + 1.2vh);
  color: #0f1359;
  font-weight: 900;
  top: -3vw;
  right: 4vw;
}
.SignalPic {
  height: 11vw;
  margin: 0 auto;
  margin-bottom: 1vw;
}

#SignalContentDiv .SignalPic {
  height: 11vw;
  margin: 0 auto;
  margin-bottom: 1.8vw;
  margin-top: 1.8vw;
}

.TimesPic {
  width: 14vw;
  padding: 0;
  padding-bottom: 1vh;
}

#TimesContentDiv div {
  border-left: 1px solid black;
  width: 17vw;
  text-align: center;
  margin-bottom: 2vh;
}

.SpecialTimesPic {
  width: 13vw;
  height: 15vw;
}

#RulesFirstContentDiv {
  width: 80vw;
  height: fit-content;
  margin: 0 auto;
  text-align: right;
  margin-top: -8vw;
}

#Rules_ClassesDiv {
  display: flex;
  justify-content: space-around;
}

#RulesContentText {
  font-weight: 1000;
  font-size: 2em;
}

#RulesContentDivFirstState p {
  margin-right: 16vw;
  width: fit-content;
  font-size: 1vw;
}

#RulesContentDivFirstStatePage {
  /*margin-right: 12vw;*/
  margin: 0 auto;
  margin-top: 3vw;
  display: flex;
  flex-wrap: wrap;
  width: 60vw;
  height: 30vw;
  justify-content: space-evenly;
}
.RulesFirstStatePageButton {
  font-weight: 1000;
  width: 15vw;
  margin: 1vw;
  font-size: 1.4vw;
  height: 10vw;
  border: none;
  box-shadow: 0.3vw 0.3vw 0.5vw gray;
  border-radius: 1vw;
  background-color: white;
  transition: 0.4s;
}
.RulesFirstStatePageButton:hover {
  transform: scale(1.1);
  transition: 0.2s;
  cursor: pointer;
}
