#RolesMain {
  width: calc(100% - 15vw);
  position: absolute;
  top: 2vw;
  right: 15vw;
  display: flex;
  direction: rtl;
  flex-direction: column;
}

#RolesMainButtonDiv {
  display: flex;
  direction: rtl;
  justify-content: space-evenly;
  margin-right: 4vw;
  margin-top: 0vw;
  height: 100px;
  width: 65vw;
}

.RolesMainButton {
  font-size: 1.5vw;
  width: 10.4vw;
  border: none;
  background-color: rgb(220, 220, 220);
  height: fit-content;
  padding: 0.4em 0em 0.4em 0em;
  transition: 0.5s;
  transform: skewX(20deg);
}

.RolesMainButton p {
  transform: skewX(-20deg);
  padding: 0;
  margin: 0;
}

.RolesMainButtonActive {
  background-color: rgb(150, 150, 150);
}
.HowToGainPointsDiv {
  margin-top: 0;
  overflow-y: scroll;
  direction: ltr;
  padding-right: 1vw;
  margin-right: 3vw;
  height: 65vh;
}
.HowToGainPointsDiv::-webkit-scrollbar {
  width: 0.5vw;
}
.HowToGainPointsDiv::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 10vw;
}

/* Handle */
.HowToGainPointsDiv::-webkit-scrollbar-thumb {
  background: rgba(100, 100, 100, 0.3);
  border-radius: 10vw;
}

/* Handle on hover */
.HowToGainPointsDiv::-webkit-scrollbar-thumb:hover {
  background: rgba(100, 100, 100, 0.6);
  cursor: pointer;
}

/* Handle on active */
.HowToGainPointsDiv::-webkit-scrollbar-thumb:active {
  background: rgba(100, 100, 100, 0.7);
  cursor: pointer;
}

#RolesContentDiv {
  width: 70vw;
  height: fit-content;
  margin: 0 auto;
  text-align: right;
}
#RolesFirstContentDiv {
  width: 80vw;
  height: fit-content;
  margin: 0 auto;
  text-align: right;
  margin-top: -8vw;
}

#Roles_ClassesDiv {
  display: flex;
  justify-content: space-around;
}

#RolesContentText {
  font-weight: 1000;
  font-size: 2em;
}

#RolesContentDivFirstState p {
  margin-right: 16vw;
  width: fit-content;
  font-size: 1vw;
}

#RolesContentDivFirstStatePage {
  /*margin-right: 12vw;*/
  margin: 0 auto;
  margin-top: 3vw;
  display: flex;
  flex-wrap: wrap;
  width: 60vw;
  height: 30vw;
  justify-content: space-evenly;
}
.RolesFirstStatePageButton {
  font-weight: 1000;
  width: 15vw;
  margin: 1vw;
  font-size: 1.4vw;
  height: 10vw;
  border: none;
  box-shadow: 0.3vw 0.3vw 0.5vw gray;
  border-radius: 1vw;
  background-color: white;
  transition: 0.4s;
}
.RolesFirstStatePageButton:hover {
  transform: scale(1.1);
  transition: 0.2s;
  cursor: pointer;
}

#gainPointsList {
  margin-right: 1vw;
}
#gainPointsList li {
  margin-bottom: 1.2vw;
}
