#BocciaClassesMain {
  width: calc(100% - 15vw);
  position: absolute;
  top: 2vw;
  right: 15vw;
  display: flex;
  direction: rtl;
  flex-direction: column;
}

#BocciaClassesMainButtonDiv {
  display: flex;
  direction: rtl;
  justify-content: space-evenly;
  margin-right: 4vw;
  margin-top: 0vw;
  height: 100px;
  width: 55vw;
}

.BocciaClassesMainButton {
  font-size: 1.5vw;
  width: 8.6vw;
  border: none;
  background-color: rgb(220, 220, 220);
  height: fit-content;
  padding: 0.4em 0em 0.4em 0em;
  transition: 0.5s;
  transform: skewX(20deg);
}

.BocciaClassesMainButton p {
  transform: skewX(-20deg);
  padding: 0;
  margin: 0;
}

.BocciaClassesMainButtonActive {
  background-color: rgb(150, 150, 150);
}

.ContentDivClasses::-webkit-scrollbar {
  width: 0.5vw;
}
.ContentDivClasses::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 10vw;
}

/* Handle */
.ContentDivClasses::-webkit-scrollbar-thumb {
  background: rgba(100, 100, 100, 0.3);
  border-radius: 10vw;
}

/* Handle on hover */
.ContentDivClasses::-webkit-scrollbar-thumb:hover {
  background: rgba(100, 100, 100, 0.6);
  cursor: pointer;
}

/* Handle on active */
.ContentDivClasses::-webkit-scrollbar-thumb:active {
  background: rgba(100, 100, 100, 0.7);
  cursor: pointer;
}

#BocciaClassesContentDiv {
  width: 70vw;
  height: fit-content;
  margin: 0 auto;
  text-align: right;
}
#BocciaClassesFirstContentDiv {
  width: 80vw;
  height: fit-content;
  margin: 0 auto;
  text-align: right;
  margin-top: -8vw;
}
#BocciaClasses_ClassesDiv {
  display: flex;
  justify-content: space-around;
}

#BocciaClassesContentText {
  font-weight: 1000;
  font-size: 2em;
}

#BocciaClassesContentDivFirstState p {
  margin-right: 16vw;
  width: fit-content;
  font-size: 1vw;
}

#BocciaClassesContentDivFirstStatePage {
  /*margin-right: 12vw;*/
  margin: 0 auto;
  margin-top: 3vw;
  display: flex;
  flex-wrap: wrap;
  width: 60vw;
  height: 30vw;
  justify-content: space-evenly;
}
.BocciaClassesFirstStatePageButton {
  font-weight: 1000;
  width: 15vw;
  margin: 1vw;
  font-size: 1.4vw;
  height: 10vw;
  border: none;
  box-shadow: 0.3vw 0.3vw 0.5vw gray;
  border-radius: 1vw;
  background-color: white;
  transition: 0.4s;
}
.BocciaClassesFirstStatePageButton:hover {
  transform: scale(1.1);
  transition: 0.2s;
  cursor: pointer;
}
